import React, { FC } from 'react';

import { Button, Icon } from 'antd';

import { PDFDownloadLink } from '@react-pdf/renderer';

import { useEditorContext } from 'state/editor';

import { ElementUnion } from 'models';

import { DocumentPDF } from 'components/documentPDF';
import BlockComponent from './components/block/block';

// import { VorlageComponent } from '../vorlage';

import './styles.css';

export interface EditorProps {}
const settingsComponents = (activeElement: ElementUnion, handleChangeSettings: any): any => ({
  block: (
    <BlockComponent activeElement={activeElement} handleChangeSettings={handleChangeSettings} />
  ),
});

const EditorComponent: FC<EditorProps> = () => {
  const { pages, actions, activeId, activeElement, isDone } = useEditorContext();

  const handleChangeSettings = (style: string, value: string, type: string) => {
    actions.handleChangeSettings(
      activeId.page,
      activeId.id,
      style,
      value,
      type,
      activeElement as ElementUnion,
    );
  };

  return (
    <div className="editor-container">
      <Button.Group>
        {!isDone && (
          <Button onClick={actions.addPage}>
            <Icon type="file" theme="filled" />
            Add page
          </Button>
        )}
        <PDFDownloadLink document={<DocumentPDF pages={pages} />} fileName="somename.pdf">
          {({ blob, url, loading, error }) =>
            loading ? (
              'Loading document...'
            ) : (
              <Button>
                Save
                <Icon type="save" theme="filled" />
              </Button>
            )
          }
        </PDFDownloadLink>

        {!isDone && (
          <Button onClick={actions.togglePreview}>
            Preview
            <Icon type="eye" theme="filled" />
          </Button>
        )}
      </Button.Group>

      {!isDone && !!activeElement && (
        <div className="editor-settings">
          {settingsComponents(activeElement, handleChangeSettings).block}
        </div>
      )}

      {/* <VorlageComponent /> */}
    </div>
  );
};

export { EditorComponent };
