import React, { FC, useState } from 'react';

import { Button, InputNumber } from 'antd';
import ChromePicker from 'react-color/lib/components/chrome/Chrome';

import { ELEMENT, ElementUnion } from 'models';
import './styles.css';

export interface BlockProps {
  handleChangeSettings: (style: string, value: string, type: string) => void;
  activeElement: ElementUnion;
}

const popover = {
  position: 'absolute',
  zIndex: '2',
};
const cover = {
  position: 'fixed',
  top: '0px',
  right: '0px',
  bottom: '0px',
  left: '0px',
};

const setColor = (type: string, style: any): any => {
  if (type === ELEMENT.BLOCK) {
    return { type: 'backgroundColor', color: style.backgroundColor };
  } else if (type === ELEMENT.BORDER) {
    return { type: 'border', color: style.border.split(' ')[2] };
  } else return null;
};

const BlockComponent: FC<BlockProps> = ({ handleChangeSettings, activeElement }) => {
  const { size, position, style, type } = activeElement;

  const [displayColorPicker, setDisplayColorPicker] = useState(false);

  const handleClick = () => {
    setDisplayColorPicker(!displayColorPicker);
  };

  const handleClose = () => {
    setDisplayColorPicker(false);
  };

  const colorActive = setColor(type, style);
  return (
    <div className="block-settings-editor">
      <div className="fields">
        <span>Width:</span>
        <InputNumber
          value={size.width}
          size="small"
          onChange={(value: any) => handleChangeSettings('width', value, 'size')}
        />
      </div>
      <div className="fields">
        <span>Height:</span>
        <InputNumber
          value={size.height}
          size="small"
          onChange={(value: any) => handleChangeSettings('height', value, 'size')}
        />
      </div>
      <div className="fields">
        <span>X:</span>
        <InputNumber
          value={position.x}
          size="small"
          onChange={(value: any) => handleChangeSettings('x', value, 'position')}
        />
      </div>
      <div className="fields">
        <span>Y:</span>
        <InputNumber
          value={position.y}
          size="small"
          onChange={(value: any) => handleChangeSettings('y', value, 'position')}
        />
      </div>
      {colorActive && (
        <>
          <Button icon="bg-colors" className="icon-settings" onClick={handleClick} />
          {displayColorPicker ? (
            <div style={popover as any}>
              <div style={cover as any} onClick={handleClose} />
              <ChromePicker
                color={colorActive}
                onChangeComplete={(color: any) =>
                  handleChangeSettings(colorActive.type, color.hex, 'style')
                }
              />
            </div>
          ) : null}
        </>
      )}
    </div>
  );
};

export default BlockComponent;
