import React, { FC } from 'react';

import { PDFViewer } from '@react-pdf/renderer';

import { DocumentPDF } from 'components/documentPDF';
import { ElementControlComponent } from 'components/elementControl';
import { elements } from 'components/elements';

import { DocumentPage } from 'models';

export interface LeftPanelProps {
  pages: DocumentPage[];
  showPreview: boolean;
}

const LeftPanel: FC<LeftPanelProps> = props => {
  const { pages, showPreview } = props;

  return (
    <div className="left-panel">
      {Object.values(elements).map(({ control }) => (
        <ElementControlComponent key={control.name} control={control} />
      ))}

      {showPreview && (
        <PDFViewer className="pdf-viewer">
          <DocumentPDF pages={pages} />
        </PDFViewer>
      )}
    </div>
  );
};

export { LeftPanel };
