import React, { FC } from 'react';

import { Modal } from 'antd';

export interface DialogProps {
  open: boolean;
  title?: string;
  content: any;
  onClose: (e: React.MouseEvent<HTMLElement>) => void;
  onAction: (e: React.MouseEvent<HTMLElement>) => void;
}

const DialogComponent: FC<DialogProps> = props => {
  const { open, title, content, onAction, onClose } = props;

  return (
    <Modal visible={open} title={title} onOk={onAction} onCancel={onClose}>
      {content}
    </Modal>
  );
};

export { DialogComponent };
