import React, { FC } from 'react';

import { DndProvider } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';

import { Layout } from 'antd';

import { Breadcrumb } from 'components/breadcrumb';
import { EditorComponent } from 'components/editor';
import { PageComponent } from 'components/page';

import { LeftPanel } from './components';

import { useEditorContext } from 'state/editor';

import './styles.css';

export interface DocumentProps {}

const DocumentPageComponent: FC<DocumentProps> = () => {
  const { pages, isDone, showPreview, actions } = useEditorContext();

  return (
    <Layout className="document">
      <Layout.Header className="header">
        <Breadcrumb />
      </Layout.Header>

      <Layout.Content className="content">
        <DndProvider backend={HTML5Backend}>
          {!isDone && <LeftPanel pages={pages} showPreview={showPreview} />}

          <div className="pages">
            {pages.map((page, index: number) => (
              <PageComponent
                key={index}
                pageIndex={index}
                page={page}
                actions={actions}
                isDone={isDone}
              />
            ))}
          </div>

          <EditorComponent />
        </DndProvider>
      </Layout.Content>

      <Layout.Footer className="footer">Binale Software 2019</Layout.Footer>
    </Layout>
  );
};

export { DocumentPageComponent };
